// src/components/coin-flip/coin-flip-data-access.tsx
'use client'

import { Program, AnchorProvider } from '@coral-xyz/anchor'
import { useConnection } from '@solana/wallet-adapter-react'
import { Cluster, PublicKey, SystemProgram, LAMPORTS_PER_SOL } from '@solana/web3.js'
import { useMutation, useQuery } from '@tanstack/react-query'
import { useCallback, useMemo } from 'react'
import BN from 'bn.js'
import { useCluster } from '../cluster/cluster-data-access'
import { useAnchorProvider } from '../solana/solana-provider'
import { useTransactionToast } from '../use-transaction-toast'
import { toast } from 'sonner'
import { SendTransactionError } from '@solana/web3.js'
import { IDL, PROGRAM_ID } from '@/lib/idl'

// Define the types based on your IDL
type CoinFlipBackend = typeof IDL

// Helper functions
function getCoinFlipProgram(provider: AnchorProvider) {
  return new Program(IDL as any, provider)
}

function getCoinFlipProgramId(): PublicKey {
  return new PublicKey(PROGRAM_ID)
}

// Helper to get transaction logs for errors
async function getTransactionLogs(error: any, connection: any) {
  if (error instanceof SendTransactionError) {
    try {
      return await error.getLogs(connection)
    } catch {
      return null
    }
  }
  return error.logs || null
}

interface InitializeArgs {
  owner: PublicKey
  fee_percentage: number
}

interface DepositArgs {
  owner: PublicKey
  amount: number
}

interface WithdrawArgs {
  owner: PublicKey
  amount: number
}

export function useCoinFlipProgram() {
  const { connection } = useConnection()
  const { cluster } = useCluster()
  const provider = useAnchorProvider()
  const transactionToast = useTransactionToast()

  const programId = useMemo(() => getCoinFlipProgramId(), [])
  const program = useMemo(() => getCoinFlipProgram(provider), [provider])

  const accounts = useQuery({
    queryKey: ['coin_flip', 'all', { cluster }],
    queryFn: () => (program.account as any).gameState.all(),
  })

  const getProgramAccount = useQuery({
    queryKey: ['get-program-account', { cluster }],
    queryFn: () => connection.getParsedAccountInfo(programId),
  })

  async function checkAccountBalances(accounts: Record<string, PublicKey>) {
    const balances: Record<string, number> = {}
    for (const [name, pubkey] of Object.entries(accounts)) {
      try {
        balances[name] = await connection.getBalance(pubkey)
      } catch {
        balances[name] = 0
      }
    }
    return balances
  }

  const initialize = useMutation<string, Error, InitializeArgs>({
    mutationKey: ['game_state', 'initialize', { cluster }],
    mutationFn: async ({ owner, fee_percentage }) => {
      const [gameState] = PublicKey.findProgramAddressSync(
        [Buffer.from('gamestate'), owner.toBuffer()],
        programId
      )
      await checkAccountBalances({ owner, gameState })

      try {
        return await (program.methods as any)
          .initialize(new BN(fee_percentage))
          .accounts({
            owner,
            gameState,
            systemProgram: SystemProgram.programId,
          })
          .rpc()
      } catch (error: any) {
        await getTransactionLogs(error, connection)
        throw error
      }
    },
    onSuccess: (signature) => {
      transactionToast(signature)
      accounts.refetch()
    },
    onError: async (error) => {
      await getTransactionLogs(error, connection)
      toast.error(`Failed to initialize: ${error.message}`)
    },
  })

  const deposit_to_gamestate = useMutation<string, Error, DepositArgs>({
    mutationKey: ['game_state', 'deposit', { cluster }],
    mutationFn: async ({ owner, amount }) => {
      const [gameState] = PublicKey.findProgramAddressSync(
        [Buffer.from('gamestate'), owner.toBuffer()],
        programId
      )
      await checkAccountBalances({ owner, gameState })

      try {
        return await (program.methods as any)
          .deposit(new BN(amount))
          .accounts({ 
            owner, 
            gameState, 
            systemProgram: SystemProgram.programId 
          })
          .rpc()
      } catch (error: any) {
        await getTransactionLogs(error, connection)
        throw error
      }
    },
    onSuccess: (signature) => {
      transactionToast(signature)
      accounts.refetch()
    },
    onError: async (error) => {
      await getTransactionLogs(error, connection)
      toast.error(`Failed to deposit: ${error.message}`)
    },
  })

  const withdraw = useMutation<string, Error, WithdrawArgs>({
    mutationKey: ['game_state', 'withdraw', { cluster }],
    mutationFn: async ({ owner, amount }) => {
      const [gameState] = PublicKey.findProgramAddressSync(
        [Buffer.from('gamestate'), owner.toBuffer()],
        programId
      )
      await checkAccountBalances({ owner, gameState })

      try {
        return await (program.methods as any)
          .withdraw(new BN(amount))
          .accounts({ 
            owner, 
            gameState, 
            systemProgram: SystemProgram.programId 
          })
          .rpc()
      } catch (error: any) {
        await getTransactionLogs(error, connection)
        throw error
      }
    },
    onSuccess: (signature) => {
      transactionToast(signature)
      accounts.refetch()
    },
    onError: async (error) => {
      await getTransactionLogs(error, connection)
      toast.error(`Failed to withdraw: ${error.message}`)
    },
  })

  return {
    program,
    programId,
    accounts,
    getProgramAccount,
    initialize,
    deposit_to_gamestate,
    withdraw,
  }
}

export function useCoinFlipProgramAccount({
  user,
  owner,
  gameNumber
}: {
  user: PublicKey
  owner: PublicKey
  gameNumber?: number
}) {
  const { connection } = useConnection()
  const { cluster } = useCluster()
  const transactionToast = useTransactionToast()
  const { program } = useCoinFlipProgram()

  const gameStateQuery = useQuery({
    queryKey: ['game_state', 'fetch', { cluster, owner: owner.toString() }],
    queryFn: async () => {
      if (owner.equals(new PublicKey('11111111111111111111111111111111'))) {
        throw new Error('Invalid owner')
      }
      const [gameState] = PublicKey.findProgramAddressSync(
        [Buffer.from('gamestate'), owner.toBuffer()],
        (program as any).programId
      )
      return await (program.account as any).gameState.fetch(gameState)
    },
    enabled: !!program && !!owner && !owner.equals(new PublicKey('11111111111111111111111111111111')),
  })

  // Use the current totalGames count (which will be the game number for the next bet)
  const currentGameNumber = useMemo(() => 
    gameNumber !== undefined ? gameNumber : (gameStateQuery.data?.totalGames?.toNumber() || 0), 
    [gameNumber, gameStateQuery.data?.totalGames]
  )

  const [gameState] = useMemo(() => PublicKey.findProgramAddressSync(
    [Buffer.from('gamestate'), owner.toBuffer()],
    (program as any).programId
  ), [owner, program])

  // IMPORTANT: Based on your IDL, the bet_account PDA uses game_state.total_games directly
  // This means we need to use the current total_games value from the game state
  const [betAccount] = useMemo(() => {
    // According to your IDL, the seed is the current total_games value from game_state
    // This is automatically handled by Anchor, but for manual derivation:
    const gameNumber = gameStateQuery.data?.totalGames?.toNumber() || 0
    const gameNumberBuf = new BN(gameNumber).toArrayLike(Buffer, 'le', 8)
    return PublicKey.findProgramAddressSync(
      [Buffer.from('bet_account'), user.toBuffer(), gameNumberBuf],
      (program as any).programId
    )
  }, [user, gameStateQuery.data?.totalGames, program])

  async function checkAllBalances() {
    const accounts = { user, owner, gameState, betAccount }
    const balances: Record<string, number> = {}
    for (const [name, pubkey] of Object.entries(accounts)) {
      try {
        balances[name] = await connection.getBalance(pubkey)
      } catch {
        balances[name] = 0
      }
    }
    return balances
  }

  const place_bet = useMutation<string, Error, { amount: number; choice: number }>({
    mutationKey: ['bet_account', 'place_bet', { cluster, betAccount: betAccount.toString() }],
    mutationFn: async ({ amount, choice }) => {
      if (![0, 1].includes(choice)) throw new Error('Choice must be 0 or 1')
      if (user.equals(new PublicKey('11111111111111111111111111111111'))) throw new Error('Invalid user public key')
      if (owner.equals(new PublicKey('11111111111111111111111111111111'))) throw new Error('Invalid owner public key')

      await checkAllBalances()

      // Fetch fresh game state data right before transaction
      const gameStateData = await (program.account as any).gameState.fetch(gameState)
       
      // Calculate expected winnings
      const payout = amount * 2
      const fee = Math.floor((payout * gameStateData.feePercentage.toNumber()) / 100)
      const userWinnings = payout - fee

      // Check if game state has enough funds
      if (userWinnings > gameStateData.totalAmount.toNumber()) {
        throw new Error(`Insufficient funds in game state. Available: ${gameStateData.totalAmount.toString()}, Required: ${userWinnings}`)
      }

      // Use snake_case method name as per your IDL
      return await (program.methods as any)
        .place_bet(new BN(amount), choice)
        .accounts({
          user,
          owner,
          gameState,
          // betAccount is automatically derived by Anchor based on the PDA seeds in IDL
          systemProgram: SystemProgram.programId,
        })
        .rpc()
    },
    onSuccess: async (signature) => {
      transactionToast(signature)
      await gameStateQuery.refetch()
    },
    onError: async (error) => {
      await getTransactionLogs(error, connection)
      toast.error(`Failed to place bet: ${error.message}`)
    },
  })

  // Query should handle the case where bet account might not exist
  const accountQuery = useQuery({
    queryKey: ['bet_account', 'fetch', { cluster, betAccount: betAccount.toString(), gameNumber: currentGameNumber }],
    queryFn: async () => {
      try {
        return await (program.account as any).betAccount.fetch(betAccount)
      } catch (error: any) {
        // Return null if account doesn't exist instead of undefined
        if (error.message?.includes('Account does not exist')) {
          return null
        }
        throw error
      }
    },
    enabled: !!program && !!betAccount && !!gameStateQuery.data,
    retry: false, // Don't retry on account not found
  })

  return {
    place_bet,
    currentGameNumber,
    gameStateData: gameStateQuery.data,
    gameState,
    betAccount,
    accountQuery,
    checkAllBalances,
  }
}